<template>
  <el-upload
    class="avatar-uploader"
    action="fake"
    :on-error="handleAvatarUpload">
    <img
      v-if="avatar"
      :src="avatar"
      class="avatar">
    <i
      v-else
      class="el-icon-plus avatar-uploader-icon">
    </i>
  </el-upload>
</template>

<script>
import { defineComponent, ref, watch } from 'vue'
import { ElUpload } from 'element-plus'
export default defineComponent({
  components: {
    ElUpload
  },
  props: {
    file: {
      type: Object,
    },
    avatar: {
      type: String,
    }
  },
  setup(props, { attrs, slots, emit }) {

    const avatar = ref('')
    const handleAvatarUpload = (_, file) => {
      avatar.value = URL.createObjectURL(file.raw);
      emit('update:file', file)
    }
    avatar.value = props.avatar

    return {
      avatar,
      handleAvatarUpload,
      props,
    }
  },
  watch: {
    'props.avatar'(newVal){
      this.avatar = newVal
    }
  }

})
</script>

<style lang="scss">
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
</style>
