<template>
  <item-tab
    class="profile-wrapper"
    :needTitle="false">
    <div class="profile">
      <div class="profile-content">
        <upload
          v-model:file="avatarFile"
          :avatar="GlobalData.userInfo.value.avatar">
        </upload>
        <div>
          <el-form label-position="left" label-width="80px">
            <el-form-item label="Username">
              <el-input v-model="userInfo.user_name">
              </el-input>
            </el-form-item>
            <el-form-item label="Password">
              <el-input
                type="password"
                v-model="userInfo.password">
              </el-input>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <el-button
        type="primary"
        style="width:200px;"
        @click="modifyUserInfo">
        Save
      </el-button>
    </div>
  </item-tab>
</template>

<script>
import axios from 'axios'
import { defineComponent, ref } from 'vue'
import { ElAvatar, ElInput, ElFormItem, ElForm, ElButton } from 'element-plus'
import itemTab from '@/components/item-tab/index'
import GlobalData from '@/store/index'
import upload from '@/components/upload/index'
import { register_api, upload_api } from '@/store/api'
export default defineComponent({
  components: {
    upload,
    itemTab,
    ElAvatar,
    ElInput,
    ElButton,
    ElForm,
    ElFormItem,
  },
  setup(props, { attrs, slots, emit }) {
    const avatarFile = ref(null)

    const userInfo = ref({
      user_name: '',
      password: '',
      avatar: '',
    })

    const modifyUserInfo = async () => {
      if(avatarFile.value) {
        const form = new FormData();
        form.append('file', avatarFile.value.raw);
        const avatarRes = await axios.post(upload_api, form, {
          headers: { 'Content-Type': 'multypart/form-data' },
        })
        userInfo.value.avatar = avatarRes.data.data.path
      }
      const modifyRes = await axios.put(register_api, userInfo.value)
      const modifySuccess = modifyRes.data.code === 1007
      if(modifySuccess) {
        GlobalData.userInfo.value.user_name = userInfo.value.user_name
        if(avatarFile.value) {
          GlobalData.userInfo.value.avatar = userInfo.value.avatar
        }

      }
    }

    return {
      GlobalData,
      avatarFile,
      userInfo,
      modifyUserInfo,
    }
  },
  watch: {
    'GlobalData.userInfo.value.user_name': {
      handler(newVal) {
        this.userInfo.user_name = newVal
      },
      immediate: true,
    },
  }
})
</script>

<style lang="scss">
  .profile {
    margin: 20px 0 20px 10%;

    &-content {
      display: flex;
      margin-bottom: 30px;
      div:first-child {
        margin-right: 50px;
      }
      .el-input input {
        width: 200px;
      }
    }

  }
</style>
